@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body{
	margin: 0;
	font-family: 'Roboto', sans-serif;
	min-height: 100vh;
}
.boost{background-image:url(/shortly-url/static/media/bg-boost-desktop.772154c9.svg);background-repeat:no-repeat;background-position:center;background-size:cover;background-color:#3b3054;padding:3rem;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;text-align:center;color:#bfbfbf}button{border:0;padding:1rem 3rem;border-radius:50px;cursor:pointer;background-color:#2acfcf;color:#fff}button:hover{opacity:60%}

.footer{width:100%;display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr;background-color:#232127;-webkit-justify-content:space-between;justify-content:space-between;color:#bfbfbf}.footer .collumn{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;text-align:left}.footer .collumn .social{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.footer .collumn .social a{margin:1.5rem .5rem}

.about{display:grid;grid-template-columns:1fr 1fr;padding:3rem 10rem 10rem 10rem}.text{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.info{text-align:left}.info h1{font-size:5rem;margin:0}.info p{font-size:2rem;margin:0 0 3rem 0}.filler{background-image:url(/shortly-url/static/media/illustration-working.c9b5ef54.svg);background-position:right;background-repeat:no-repeat;background-size:contain}

h1{font-size:2rem}p{font-size:1.1rem}nav{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;width:100%;-webkit-justify-content:flex-end;justify-content:flex-end}nav ul{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center}nav ul li{list-style:none;padding:0 .5rem;cursor:pointer}nav .nav{width:60%;display:-webkit-flex;display:flex;padding:0 10rem}nav .nav li{padding:0 2rem}nav .btns{display:-webkit-flex;display:flex;-webkit-flex:1 1;flex:1 1;-webkit-justify-content:center;justify-content:center}nav .btns button{background-color:rgba(0,0,0,0);border:0;padding:0 2rem;border-radius:50px;cursor:pointer;color:#bfbfbf}nav .btns .signUpBtn{background-color:#2acfcf;color:#fff}nav .btns .signUpBtn:hover{opacity:60%}

.container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:100%;z-index:10000}.container .content{background-image:url(/shortly-url/static/media/bg-shorten-desktop.0d9742f4.svg);background-repeat:no-repeat;background-position:center;background-size:cover;background-color:#3b3054;padding:3rem;border-radius:10px}.container .content form{width:100%}.container .content input{border:0;width:35vw;height:3rem;margin:0 1rem;border-radius:10px;padding:0 0.5em;font-size:1em}.container .content button{background-color:#2acfcf;color:#fff;border:0;padding:0 2em;height:3rem;border-radius:10px;cursor:pointer}.container .content button:hover{opacity:60%}.container .content .show{padding:2rem 0 0 0;color:#bfbfbf}.container .content .copy{background-color:rgba(255,255,255,0);margin:0 1rem;font-size:.7rem;border:1px dashed #bfbfbf;padding:.2rem;height:unset;border-radius:0}

.box{display:-webkit-flex;display:flex;background-color:#e6e6e6;-webkit-justify-content:center;justify-content:center;width:100%;position:relative}.title{padding-top:7rem;margin:0}.short{position:absolute;top:-8%}.stats{padding:3rem;text-align:center}.stats p{color:#bfbfbf}.cardsArea{display:grid;grid-template-columns:1fr 1fr 1fr;padding:2rem;position:relative}.card{background-color:#fff;max-width:300px;padding:2rem;margin:3rem 1rem;text-align:left;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;position:relative}.cf{margin-top:5rem !important}.cs{margin-top:7rem}.iconCard{position:absolute;top:-15%;left:5%;background-image:url(/shortly-url/static/media/icon-detailed-records.265c8aea.svg);background-color:#3b3054;background-position:center;background-repeat:no-repeat;padding:2rem;border-radius:100px}.fa{background-image:url(/shortly-url/static/media/icon-brand-recognition.c349d3b6.svg)}.sa{background-image:url(/shortly-url/static/media/icon-fully-customizable.a1532a74.svg)}.line{position:absolute;padding:0.2rem;background-color:#2acfcf;width:60%;top:200px;right:200px}

