.footer{
	width: 100%;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
	background-color: hsl(260, 8%, 14%);
	justify-content: space-between;
	color:hsl(0, 0%, 75%);

	.collumn{
		display: flex;
		align-items: center;
		flex-direction: column;
		text-align: left;

		.social{
			display: flex;
			justify-content: center;
		}
		.social a{
			margin: 1.5rem .5rem;
		}
	}
}

