.box{
	display: flex;
	background-color: hsl(0, 0%, 90%);
	justify-content: center;
	width: 100%;
	position: relative;
}
.title{
	padding-top: 7rem;
	margin: 0;
}
.short{
	position: absolute;
	top: -8%;
}
.stats{
	padding: 3rem;
	text-align: center;
}
.stats p {
	color:hsl(0, 0%, 75%);
}
.cardsArea{
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	padding: 2rem;
	position: relative;
}
.card{
	background-color: #fff;
	max-width: 300px;
	padding: 2rem;
	margin: 3rem 1rem;
	text-align:left;
	height: fit-content;
	position: relative;
}
.cf{
	margin-top: 5rem!important;
}

.cs{
	margin-top: 7rem;
}
.iconCard{
	position: absolute;
	top: -15%;
	left: 5%;
	background-image: url('../../images/icon-detailed-records.svg');
	background-color: hsl(257, 27%, 26%);
	background-position: center;
	background-repeat: no-repeat;
	padding: 2rem;
	border-radius: 100px;
}
.fa{
	background-image: url('../../images/icon-brand-recognition.svg');
}
.sa{
	background-image: url('../../images/icon-fully-customizable.svg');
}
.line{
	position: absolute;
	padding: 0.2rem;
	background-color: hsl(180, 66%, 49%);
	width: 60%;
	top: 200px;
	right: 200px;
}